  computed: {

    loadUrl: function loadUrl() {
      var _self = this;
      var url = '/index.php?c=health&a=upload_file' + GetParam() + _self.fromOutsideParm;
      /* var url='http://localhost:9494/index.php?m=home&c=attach&a=uploadImage'*/
      return url;
    },
    fromOutsideParm: function fromOutsideParm() {
      var _self = this;
      var str = '';
      if (_self.fromOutside) {
        str = '&name=' + (_self.param.name || '') + '&type=2&dept=' + GetParams.getParam('department');
      }
      return str;
    }
  },
  props: ['ips'],
  methods: {
    getLoadUrl: function getLoadUrl(url) {
      var _self = this;
      if (_self.param.if_confirm_to_workplace == 5) {
        return url + '&upload_type=2';
      } else {
        return url;
      }
    },
    today: function today() {
      var today = new Date();
      var str = "";
      str += today.getFullYear() + "-";
      var month = today.getMonth() + 1;
      if (month < 10) {
        str += "0";
      }
      str += month + "-";
      var day = today.getDate();
      if (day < 10) {
        str += "0";
      }
      str += day;
      return str;
    },
    leftDay: function leftDay(date) {
      var _self = this;
      date = Date.parse(date);
      var today = Date.parse(_self.today());
      var ms = date - today;
      var days = Math.floor(ms / (24 * 3600 * 1000));
      return days;
    },
    showHealth: function showHealth(arr) {
      var _self = this;
      if (arr instanceof Array) {
        return arr.join(',');
      } else {
        return '';
      }
    },
    changeFn: function changeFn() {
      var _self = this;

      _self.param.location = "";
    },
    focusFn: function focusFn() {
      var _self = this;
      if (_self.isIos()) {
        setTimeout(function () {
          document.querySelector('#app').scrollIntoView(false);
        }, 200);
      }
    },
    blurFn: function blurFn() {
      var _self = this;
      if (_self.isIos()) {
        setTimeout(function () {
          var scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
          window.scrollTo(0, Math.max(scrollHeight - 1, 0));
        }, 50);
      }
    },
    getData: function getData() {
      var _self = this;
      _self.load = true;

      _self.$store.dispatch("doGet", {
        url: "/index.php?c=health&a=find" + GetParam() + _self.fromOutsideParm,
        param: {},
        hideLoadMask: true
      }).then(function (oRep) {
        _self.load = false;
        if (oRep.code == 0) {
          if (!oRep.data.holidaysummary.location) {
            oRep.data.holidaysummary.location = "";
          }

          _self.param = Object.assign({}, oRep.data.holidaysummary);
          _self.dailyinfo = Object.assign({}, oRep.data.dailyinfo);
          _self.filelist = oRep.data.filelist || [];
          if (_self.dailyinfo.status) {
            _self.dailyinfo.status = _self.dailyinfo.status.split(',');
          }
          if (_self.param.status_for_contact) {
            _self.param.status_for_contact = _self.param.status_for_contact.split(',');
          }

          if (_self.param.location) {

            var f = _self.inArray(_self.param.location, _self.country);
            if (f) {
              _self.abroad = "2";
              _self.init = true;
            }
          }
        } else {
          Alert.showBox({
            str: oRep.message
          });
        }
      });
    },
    //
    countermand: function countermand() {
      this.show = false;
    },
    //
    choose: function choose() {
      this.show = true;
    },
    onChangeProvince1: function onChangeProvince1(a) {
      this.province = a.value;
      /*   if (a.value == '') {
           this.show = false;
         }*/
    },
    onChangeCity: function onChangeCity(a) {
      this.city = a.value;
    },
    onChangeArea: function onChangeArea(a) {
      this.area = a.value;
      this.show = false;
      this.param.location = this.province + this.city + this.area;
    },
    indexOfFn: function indexOfFn(val, arr) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == val) return i;
      }
      return -1;
    },
    inArray: function inArray(item, array) {
      var _self = this,
          flag = false;
      for (var i = 0; i < array.length; i++) {
        if (item == array[i]) {
          flag = true;
          break;
        }
      }
      return flag;
    },
    doSave: function doSave() {
      var _self = this;
      var infoData = Object.assign({}, _self.dailyinfo);
      var holidayData = Object.assign({}, _self.param);
      if (infoData.status) {
        infoData.status = infoData.status.join(',');
        if (infoData.status == '1') {
          infoData.desc_status = '';
        }
      }
      if (holidayData.status_for_contact) {
        holidayData.status_for_contact = holidayData.status_for_contact.join(',');
        if (holidayData.status_for_contact == '1') {
          holidayData.desc_status_for_contact = '';
        }
      }
      _self.load = true;
      if (!_self.hasBeento) {
        holidayData.city_Hubei = '';
        holidayData.date_to_Hubei = '';
        holidayData.travel_to_Hubei = '';
      }
      if (!_self.sureBack) {
        holidayData.date_to_workplace = '';
        holidayData.way_to_workplace = '';
        holidayData.ticket_number_to_workplace = '';
        holidayData.ticket_date_to_workplace = '';
      }
      if (!_self.contract_date) {
        holidayData.date_contact_Hubei_people = '';
        holidayData.desc_contact_Hubei_people = '';
      }
      if (!_self.bySelf) {
        holidayData.ticket_date_to_workplace = '';
        holidayData.ticket_num_to_workplace = '';
      }

      _self.$store.dispatch("doPost", {
        url: "/index.php?c=health&a=update" + GetParam() + _self.fromOutsideParm,
        param: {
          dailyinfo: infoData,
          holidaysummary: holidayData
        },
        hideLoadMask: true
      }).then(function (oRep) {
        _self.load = false;
        if (oRep.code == 0) {
          _self.getData();
          if (!_self.hasSave) {
            localStorage.setItem('hasSaveHealth', '1');
            _self.hasSave = true;
          }
        }
        Alert.showBox({
          str: oRep.message
        });
      });
    },
    isIos: function isIos() {
      var u = navigator.userAgent;
      var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      return isiOS;
    },
    handlesuccess: function handlesuccess(response, file, filelist) {
      var _self = this;
      this.load = false;
      if (response.code == 0) {
        file.uid = response.data.uid;
        file.id = response.data.id;
        _self.filelist.push(file);
      } else {
        Alert.showBox({
          str: response.message
        });
      }
    },
    handleRemove: function handleRemove(file) {
      var _self = this;
      if (file.uid) {
        file.id = file.uid;
      }

      _self.$store.dispatch("doPost", {
        url: "/index.php?c=health&a=drop_file" + GetParam() + _self.fromOutsideParm,
        param: file,
        hideLoadMask: true
      }).then(function (oRep) {
        if (oRep.code == 0) {
          var t = 0;
          for (var i = 0; i < _self.filelist.length; i++) {
            if (_self.filelist[i].uid == file.uid) {
              t = i;
              break;
            }
          }
          _self.filelist.splice(t, 1);
        }
        _self.load = false;
        Alert.showBox({
          str: oRep.message
        });
      });
    },
    beforeAvatarUpload: function beforeAvatarUpload(file) {
      this.load = true;
      var isLt2M = file.size / 1024 / 1024 < 2;
      var flag = isAsyncAwaitSupport();
      if (flag) {
        return new Promise(function (resolve, reject) {

          if (isLt2M) {
            resolve(file);
          }
          console.log(file); // 400KB,400,
          imageConversion.compressAccurately(file, 1600).then(function (res) {
            // console.log(res)
            console.log(res);
            resolve(res);
          });
        });
      } else {
        this.load = false;
        Alert.showBox({
          str: ' 2MB'
        });
        return isLt2M;
      }
    },


